.cf-content {
  height: 409px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.cf-content.disable-ani {
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
}

.flip-leave {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.flip-leave.flip-leave-active {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  -webkit-transition: -webkit-transform .4s ease-in;
  transition: -webkit-transform .4s ease-in;
  -o-transition: transform .4s ease-in;
  transition: transform .4s ease-in;
  transition: transform .4s ease-in, -webkit-transform .4s ease-in;
}

.flip-enter {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
}
.flip-enter.flip-enter-active {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  -webkit-transition: -webkit-transform .4s ease-in .4s;
  transition: -webkit-transform .4s ease-in .4s;
  -o-transition: transform .4s ease-in .4s;
  transition: transform .4s ease-in .4s;
  transition: transform .4s ease-in .4s, -webkit-transform .4s ease-in .4s;
}


